.section-intro {
    width: 100%;
    position: relative;
    height: 120rem;
    background-color: #000;
    transform-origin: 50% 70%;
    @include susy-breakpoint($s-320, $c8) {
        height: 78rem;
    }
    @include susy-breakpoint($s-768, $c8) {
        height: 100rem;
    }
    @include susy-breakpoint($s-960, $c12) {
        height: 80rem;
    }
    @include susy-breakpoint($s-1201, $c18) {
        height: 80rem;
    }
    @include susy-breakpoint($s-1601, $c18) {
        height: 108rem;
    }
    &__content {
        position: absolute;
        z-index: 100;
        left: 50%;
        top: 24rem;
        transform: translate3d(-50%,0,0);
        width: 58rem;
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
            box-sizing: border-box;
            padding: 0 2rem;
            top: 7.2rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            top: 14rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            top: 10rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            top: 10rem
        }
        @include susy-breakpoint($s-1601, $c18) {
            top: 16rem;
        }
    }
    &__sub-title {
        @extend %site-sub-title;
    }
    &__main-title {
        width: 40rem;
        margin: 0 auto 2.4rem;
        img {
            @extend .rsp-img;
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 26rem;
            margin-bottom: 1.6rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 32rem;
            margin-bottom: 1.6rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            width: 32rem;
            margin-bottom: 1.6rem;
        }
    }
    &__description {
        @extend %site-description;
    }
    &__btn-join {
        font-size: 1.8rem;
        font-weight: 900;
        color: #4600a7;
        height: 3.6rem;
        width: 12rem;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #fff;
        border-radius: 6rem;
        border: 0;
        padding: 0;
        position: relative;
        margin: 0 auto;
        transform: translate3d(0,0,0);
        animation-name: MoveUpDown;
        animation-duration: 500ms;
        animation-iteration-count: infinite;
        animation-timing-function: ease-in-out;
        animation-fill-mode: both;
        animation-direction: alternate-reverse;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 1.6rem;
            width: 11rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            font-size: 1.6rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            font-size: 1.6rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            font-size: 1.6rem;
        }
        &::after {
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translate3d(-50%,90%,0);
            border-top: solid 1rem #fff;
            border-right: solid 1rem transparent;
            border-bottom: solid 1rem transparent;
            border-left: solid 1rem transparent;
            content: "";
        }
    }
    &__shoe-slide-wrap {
        width: 36rem;
        height: 24.9rem;
        left: 50%;
        top: 50%;
        transform: translate3d(-50%,68%,0);
        position: absolute;
        z-index: 40;
        @include susy-breakpoint($s-320, $c8) {
            width: 36rem * 0.7;
            height: 24.9rem * 0.7;
            transform: translate3d(-50%,55%,0);
        }
        @include susy-breakpoint($s-768, $c8) {
            width: 36rem * 0.9;
            height: 24.9rem * 0.9;
            transform: translate3d(-50%,55%,0);
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 36rem * 0.7;
            height: 24.9rem * 0.7;
            transform: translate3d(-50%,47%,0);
        }
        @include susy-breakpoint($s-1201, $c18) {
            width: 36rem * 0.8;
            height: 24.9rem * 0.8;
            transform: translate3d(-50%,30%,0);
        }
        @include susy-breakpoint($s-1601, $c18) {
            transform: translate3d(-50%,40%,0);
        }
    }
    &__shoe {
        width: 100%;
        img {
            max-width: 100%;
            display: block;
            margin: 0;
        }
    }
    &__mask {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 120rem;
        background: transparent url('../images/intro-mask.png') repeat-x center bottom;
        background-size: 192rem auto;
        z-index: 50;
        @include susy-breakpoint($s-320, $c8) {
            height: 78rem;
            background-image: url('../images/intro-mask-m.png');
            background-size: 41.4rem auto;
        }
        @include susy-breakpoint($s-768, $c8) {
            height: 104rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            height: 80rem;
            background-size: 145rem auto;
        }
        @include susy-breakpoint($s-1201, $c18) {
            height: 80rem;
            background-size: 145rem auto;
        }
        @include susy-breakpoint($s-1601, $c18) {
            height: 108rem;
        }
    }
    &__door-bg {
        width: 13%;
        left: 50%;
        top: 50%;
        position: absolute;
        transform: translate3d(-50%,-4%,0);
        z-index: 20;
        img {
            max-width: 100%;
            display: block;
            margin: 0;
        }
        @include susy-breakpoint($s-320, $c8) {
            transform: translate3d(-50%,0,0);
            width: 16.5rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            width: 22.5rem;
            transform: translate3d(-50%,0,0);
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 20rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            width: 20rem;
        }
        @include susy-breakpoint($s-1601, $c18) {
            width: 23rem;
            transform: translate3d(-50%,-2%,0);
        }
    }
    &__door-outline {
        width: 20rem;
        left: 50%;
        top: 56%;
        position: absolute;
        transform: translate3d(-50%,0,0);
        z-index: 30;
        img {
            position: absolute;
            width: 100%;
            max-width: 100%;
            display: block;
            margin: 0;
            transform-origin: 50%;
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 16rem;
            top: 52%;
        }
        @include susy-breakpoint($s-768, $c8) {
            width: 21rem;
            top: 51%;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 16rem;
            top: 50%;
        }
        @include susy-breakpoint($s-1201, $c18) {
            width: 10.4%;
            top: 50%;
        }
        @include susy-breakpoint($s-1601, $c18) {
            width: 19rem;
            top: 51.5%;
        }
    }
}

@keyframes MoveUpDown {
    0% {
        transform: translate3d(0,0,0);
    }
    100% {
        transform: translate3d(0,15%,0);
    }
}