.site-header {
    width: 100%;
    height: 7.2rem;
    background-color: #000;
    padding-left: 2.4rem;
    padding-right: 2.4rem;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 350;
    @include susy-breakpoint($s-320, $c8) {
        height: 4.8rem;
        padding-left: 2rem;
        padding-right: 2rem;
        justify-content: center;
    }
    @include susy-breakpoint($s-768, $c8) {
        height: 4.8rem;
    }
    @include susy-breakpoint($s-960, $c12) {
        height: 4.8rem;
    }
    @include susy-breakpoint($s-1201, $c18) {
        height: 4.8rem;
    }
    &__logo {
        width: 13.3rem;
        height: 2.7rem;
        background: transparent url('../images/area02-logo.svg') no-repeat 0 0;
        background-size: cover;
        @include susy-breakpoint($s-320, $c8) {
            width: 9.1rem;
            height: 1.8rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            width: 13.3rem * 0.7;
            height: 2.7rem * 0.7;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 13.3rem * 0.7;
            height: 2.7rem * 0.7;
        }
        @include susy-breakpoint($s-1201, $c18) {
            width: 13.3rem * 0.7;
            height: 2.7rem * 0.7;
        }
        a {
            display: block;
            width: 100%;
            height: 100%;
        }
    }
    &__nav {
        display: flex;
        align-items: center;
        @include susy-breakpoint($s-320, $c8) {
            flex-direction: column;
            width: 16rem;
            height: calc(100vh - 4.8rem);
            background-color: #000;
            position: fixed;
            right: 0;
            top: 4.8rem;
            transform: translate3d(100%,0,0);
            transition: all 500ms ease-in-out;
            box-sizing: border-box;
            padding: 2rem;
            align-items: flex-end;
            &.active {
                transform: translate3d(0,0,0);
            }
        }
    }
    &__btn-toggle {
        width: 2rem;
        height: 2rem;
        position: absolute;
        right: 2rem;
        top: 50%;
        transform: translate3d(0,-50%,0);
        background-color: transparent;
        border: 0;
        padding: 0;
        display: none;
        @include susy-breakpoint($s-320, $c8) {
            display: block;
        }
        span {
            content: "";
            width: 100%;
            height: 0.1rem;
            background-color: #fff;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate3d(-50%,-50%,0);
            transition: opacity 500ms ease-in-out;
        }
        &::before,
        &::after {
            content: "";
            width: 100%;
            height: 0.1rem;
            background-color: #fff;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate3d(-50%,-50%,0);
            transition: all 500ms ease-in-out;
            transform-origin: 50%;
        }
        &::before {
            transform: translate3d(-50%,-0.6rem,0);
        }
        &::after {
            transform: translate3d(-50%,0.6rem,0);
        }
        &.active {
            span {
                opacity: 0;
            }
            &::before {
                transform: translate3d(-50%,0,0) rotate(45deg);
            }
            &::after {
                transform: translate3d(-50%,0,0) rotate(-45deg);
            }
        }
    }
    &__btn {
        color: #fff;
        font-size: 1.8rem;
        font-weight: 400;
        transition: all 500ms ease;
        opacity: 0.5;
        &:not(:last-child) {
            margin-right: 4rem;
        }
        &:hover {
            opacity: 1;
        }
        &.home {
            img {
                width: 2rem;
                @include susy-breakpoint($s-320, $c8) {
                    width: 1.6rem;
                }
                @include susy-breakpoint($s-768, $c8) {
                    width: 1.6rem;
                }
                @include susy-breakpoint($s-960, $c12) {
                    width: 1.6rem;
                }
                @include susy-breakpoint($s-1201, $c18) {
                    width: 1.6rem;
                }
            }
        }
        &.active {
            opacity: 1;
        }
        @include susy-breakpoint($s-320, $c8) {
            font-size: 1.4rem;
            &:not(:last-child) {
                margin-bottom: 2.4rem;
            }
            &:not(:last-child) {
                margin-right: 0;
            }
        }
        @include susy-breakpoint($s-768, $c8) {
            font-size: 1.4rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            font-size: 1.4rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            font-size: 1.4rem;
        }
    }
    &__btn-home {
        display: block;
        width: 2rem;
        @include susy-breakpoint($s-320, $c8) {
            width: 1.8rem;
            position: absolute;
            right: 2rem;
            top: 50%;
            transform: translate3d(0,-50%,0);
        }
        @include susy-breakpoint($s-768, $c8) {
            width: 1.8rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 1.8rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            width: 1.8rem;
        }
        img {
            max-width: 100%;
            display: block;
            margin: 0;
        }
    }
}