.section-dark {
  width: 100%;
  height: 120rem;
  background: #000 url('../images/intro-dark.jpg') repeat-x center bottom;
  background-size: 192rem auto;
  opacity: 1;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 400;
  transition: opacity 1000ms ease-in-out;
  overflow: hidden;
  @include susy-breakpoint($s-320, $c8) {
    background-image: url('../images/intro-dark-m.jpg');
    background-position: center bottom;
    background-size: 41.4rem auto;
    height: 78rem;
  }
  @include susy-breakpoint($s-768, $c8) {
    height: 104rem;
  }
  @include susy-breakpoint($s-960, $c12) {
    background-size: 145rem auto;
    background-position: center bottom;
    height: 80rem;
  }
  @include susy-breakpoint($s-1201, $c18) {
    background-size: 145rem auto;
    background-position: center bottom;
    height: 80rem;
  }
  @include susy-breakpoint($s-1601, $c18) {
    height: 108rem;
  }
  &.end {
    opacity: 0;
    pointer-events: none;
  }
  &__content {
    position: absolute;
    width: 48rem;
    left: 50%;
    top: 5%;
    transform: translate3d(-50%,0,0);
    @include susy-breakpoint($s-320, $c8) {
      width: 100%;
      box-sizing: border-box;
      padding-left: 2rem;
      padding-right: 2rem;
      top: 2rem;
    }
    @include susy-breakpoint($s-768, $c8) {
      top: 2.4rem;
    }
    @include susy-breakpoint($s-960, $c12) {
      top: 2rem;
    }
    @include susy-breakpoint($s-1201, $c18) {
      top: 2rem;
    }
  }
  &__logo {
    width: 6.2rem;
    height: 7.1rem;
    margin: 0 auto 4rem;
    @include susy-breakpoint($s-320, $c8) {
      width: 6.2rem * 0.7;
      height: 7.1rem* 0.7;
      margin-bottom: 1.6rem;
    }
    @include susy-breakpoint($s-768, $c8) {
      width: 6.2rem * 0.8;
      height: 7.1rem* 0.8;
      margin-bottom: 2.4rem;
    }
    @include susy-breakpoint($s-960, $c12) {
      width: 6.2rem * 0.7;
      height: 7.1rem* 0.7;
      margin-bottom: 1.6rem;
    }
    @include susy-breakpoint($s-1201, $c18) {
      width: 6.2rem * 0.7;
      height: 7.1rem* 0.7;
      margin-bottom: 1.6rem;
    }
    img {
      max-width: 100%;
      display: block;
      margin: 0;
    }
  }
  &__scroll-text {
    width: 100%;
    height: 24rem;
    margin-bottom: 4rem;
    position: relative;
    @include susy-breakpoint($s-320, $c8) {
      margin-bottom: 1.6rem;
    }
    @include susy-breakpoint($s-768, $c8) {
      font-size: 1.6rem;
    }
    @include susy-breakpoint($s-960, $c12) {
      margin-bottom: 0;
    }
    @include susy-breakpoint($s-1201, $c18) {
      margin-bottom: 0;
    }
    @include susy-breakpoint($s-1601, $c18) {
      margin-bottom: 2.4rem;
    }
    p {
      bottom: 0;
      position: absolute;
      left: 0;
      width: 100%;
      font-size: 1.8rem;
      color: #fff;
      text-align: center;
      opacity: 0;
      line-height: 1.6;
      @include susy-breakpoint($s-320, $c8) {
        font-size: 1.4rem;
      }
      @include susy-breakpoint($s-768, $c8) {
        font-size: 1.4rem;
      }
      @include susy-breakpoint($s-960, $c12) {
        font-size: 1.4rem;
      }
      @include susy-breakpoint($s-1201, $c18) {
        font-size: 1.4rem;
      }
    }
  }
  &__btn-close {
    display: block;
    width: 4rem;
    height: 4rem;
    border: solid 0.1rem #fff;
    background-color: #000;
    border-radius: 50%;
    position: relative;
    margin: 0 auto;
    opacity: 0;
    &::before,
    &::after {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      width: 2rem;
      height: 0.1rem;
      background-color: #fff;
    }
    &::before {
      transform: translate3d(-50%,-50%,0) rotate(45deg);
    }
    &::after {
      transform: translate3d(-50%,-50%,0) rotate(-45deg);
    }
    @include susy-breakpoint($s-320, $c8) {
      width: 3.2rem;
      height: 3.2rem;
      &::before,
      &::after {
        width: 1.6rem;
      }
    }
    @include susy-breakpoint($s-768, $c8) {
      width: 3.2rem;
      height: 3.2rem;
      &::before,
      &::after {
        width: 1.6rem;
      }
    }
    @include susy-breakpoint($s-960, $c12) {
      width: 3.2rem;
      height: 3.2rem;
      &::before,
      &::after {
        width: 1.6rem;
      }
    }
    @include susy-breakpoint($s-1201, $c18) {
      width: 3.2rem;
      height: 3.2rem;
      &::before,
      &::after {
        width: 1.6rem;
      }
    }
  }
}