.section-chance {
    background: #3c20a9 url('../images/chance-bg.jpg') repeat-x center bottom;
    background-size: 192rem auto;
    box-sizing: border-box;
    padding: 12rem 2.4rem 56rem;
    position: relative;
    @include susy-breakpoint($s-320, $c8) {
        background-image: url('../images/chance-bg-m.jpg');
        background-size: 41.4rem auto;
        padding-top: 5rem;
        padding-bottom: 36rem;
        height: 102rem;
        padding-left: 0;
        padding-right: 0;
    }
    @include susy-breakpoint($s-768, $c8) {
        padding-top: 7rem;
    }
    @include susy-breakpoint($s-960, $c12) {
        background-size: 145rem auto;
        padding-bottom: 42rem;
    }
    &__inner {
        width: 64rem;
        margin: 0 auto;
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
            box-sizing: border-box;
            padding-left: 8vw;
            padding-right: 8vw;
            padding-top: 4rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            padding-top: 5.6rem;
        }
    }
    &__sub-title {
        @extend %site-sub-title;
    }
    &__description {
        @extend %site-description;
        width: 42rem;
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
        }
    }
    &__text {
        @extend %site-text;
        br {
            display: block;
        }
    }
    &__chance {
        text-align: center;
        font-size: 1.8rem;
        color: #fff;
        margin-bottom: 1.6rem;
        strong {
            font-size: 4rem;
            padding-right: 0.4rem;
        }
        @include susy-breakpoint($s-320, $c8) {
            font-size: 1.4rem;
            strong {
                font-size: 2.4rem;
            }
        }
        @include susy-breakpoint($s-960, $c12) {
            font-size: 1.4rem;
            strong {
                font-size: 2.4rem;
            }
        }
    }
    &__detail {
        position: relative;
        margin-bottom: 2rem;
    }
    &__btn {
        border: solid 0.1rem #fff;
        font-size: 1.4rem;
        width: 7.2rem;
        background-color: transparent;
        display: block;
        margin: 0 auto;
        border-radius: 10rem;
        padding: 0.4rem 0;
        color: #fff;
        transition: all 300ms ease;
        &.active {
            border-color: #000;
            background-color: #000;
        }
    }
    &__btn-close {
        @extend %detail-content-close;
    }
    &__detail-info {
        @extend %detail-content;
    }
    &__invitation-code {
        p {
            font-size: 1.8rem;
            text-align: center;
            color: #fff;
            margin-bottom: 0.8rem;
            line-height: 1.6;
            a {
                color: #fff;
                border-bottom: 0.1rem solid #fff;
            }
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.4rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 1.6rem;
            }
        }
    }
    &__code {
        display: grid;
        grid-template-columns: 1fr 12rem;
        width: 42rem;
        margin: 0 auto 3.2rem;
        border-radius: 10rem;
        overflow: hidden;
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
            grid-template-columns: 1fr 8rem;
            margin-bottom: 1.6rem;
        }
        > input {
            font-size: 1.4rem;
            color: #471fa7;
            box-sizing: border-box;
            padding: 0;
            border: 0;
            padding: 1.2rem;
            text-align: center;
            border-radius: 0;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.2rem;
            }
        }
        > button {
            border: 0;
            border-left: solid 0.1rem #7d61c1;
            background-color: #fff;
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 1.2rem 0;
            gap: 0.8rem;
            -webkit-appearance: none;
            border-radius: 0;
            img {
                width: 3.6rem;
                @include susy-breakpoint($s-320, $c8) {
                    width: 2rem;
                }
                @include susy-breakpoint($s-768, $c8) {
                    width: 2.4rem;
                }
                @include susy-breakpoint($s-960, $c12) {
                    width: 2.4rem;
                }
                @include susy-breakpoint($s-1201, $c18) {
                    width: 2.4rem;
                }
            }
            span {
                font-size: 2.4rem;
                color: #471fa7;
                @include susy-breakpoint($s-320, $c8) {
                    font-size: 1.4rem;
                }
                @include susy-breakpoint($s-768, $c8) {
                    font-size: 1.6rem;
                }
                @include susy-breakpoint($s-960, $c12) {
                    font-size: 1.6rem;
                }
                @include susy-breakpoint($s-1201, $c18) {
                    font-size: 2rem;
                }
            }
        }
    }
    &__btn-wrap {
        width: 22rem;
        display: grid;
        grid-template-columns: 1fr;
        gap: 2.4rem;
        margin: 0 auto 2.4rem;
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
            gap: 1.6rem;
            margin-bottom: 1.6rem;
            grid-template-columns: repeat(2, 1fr);
        }
        @include susy-breakpoint($s-768, $c8) {
            gap: 2rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            gap: 2rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            gap: 2rem;
        }
    }
    &__btn-facebook,
    &__btn-line,
    &__btn-line-mobile {
        width: 100%;
        border: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 6rem;
        padding: 1.2rem 0;
        transition: all 300ms ease;
        position: relative;
        img {
            width: 3.2rem;
            margin-right: 0.4rem;
            position: relative;
            z-index: 0;
            transition: all 300ms ease;
            @include susy-breakpoint($s-320, $c8) {
                width: 2rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                width: 2.4rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                width: 2.4em;
            }
            @include susy-breakpoint($s-1201, $c18) {
                width: 2.4rem;
            }
        }
        span {
            font-size: 2.4rem;
            color: #fff;
            transition: all 300ms ease;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.4rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                font-size: 1.6rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 1.6rem;
            }
            @include susy-breakpoint($s-1201, $c18) {
                font-size: 2rem;
            }
        }
    }
    &__btn-facebook {
        background-color: #1c6deb;
        &::before {
            content: "";
            background: transparent url('../images/icon-facebook-hover@3x.png');
            background-size: cover;
            position: absolute;
            left: 50%;
            top: 50%;
            width: 3.2rem;
            height: 3.2rem;
            transform: translate3d(-206%,-50%,0);
            z-index: 10;
            opacity: 0;
            transition: all 300ms ease;
            @include susy-breakpoint($s-320, $c8) {
                width: 2rem;
                height: 2rem;
                transform: translate3d(-200%,-50%,0);
            }
            @include susy-breakpoint($s-768, $c8) {
                width: 2.4rem;
                height: 2.4rem;
                transform: translate3d(-192%,-50%,0);
            }
            @include susy-breakpoint($s-960, $c12) {
                width: 2.4em;
                height: 2.4rem;
                transform: translate3d(-192%,-50%,0);
            }
            @include susy-breakpoint($s-1201, $c18) {
                width: 2.4rem;
                height: 2.4rem;
                transform: translate3d(-225%,-50%,0);
            }
        }
        &:hover {
            background-color: #fff;
            span {
                color: #1c6deb;
            }
            img {
                opacity: 0;
            }
            &::before {
                opacity: 1;
            }
        }
    }
    &__btn-line {
        @include susy-breakpoint($s-320, $c8) {
            display: none;
        }
        background-color: #20c405;
        &::before {
            content: "";
            background: transparent url('../images/icon-line-hover@3x.png');
            background-size: cover;
            position: absolute;
            left: 50%;
            top: 50%;
            width: 3.2rem;
            height: 3.2rem;
            transform: translate3d(-206%,-50%,0);
            z-index: 10;
            opacity: 0;
            transition: all 300ms ease;
            @include susy-breakpoint($s-320, $c8) {
                width: 2rem;
                height: 2rem;
                transform: translate3d(-200%,-50%,0);
            }
            @include susy-breakpoint($s-768, $c8) {
                width: 2.4rem;
                height: 2.4rem;
                transform: translate3d(-192%,-50%,0);
            }
            @include susy-breakpoint($s-960, $c12) {
                width: 2.4em;
                height: 2.4rem;
                transform: translate3d(-192%,-50%,0);
            }
            @include susy-breakpoint($s-1201, $c18) {
                width: 2.4rem;
                height: 2.4rem;
                transform: translate3d(-225%,-50%,0);
            }
        }
        &:hover {
            background-color: #fff;
            span {
                color: #20c405;
            }
            img {
                opacity: 0;
            }
            &::before {
                opacity: 1;
            }
        }
    }
    &__btn-line-mobile {
        display: none;
        @include susy-breakpoint($s-320, $c8) {
            display: flex;
        }
        background-color: #20c405;
        &::before {
            content: "";
            background: transparent url('../images/icon-line-hover@3x.png');
            background-size: cover;
            position: absolute;
            left: 50%;
            top: 50%;
            width: 3.2rem;
            height: 3.2rem;
            transform: translate3d(-206%,-50%,0);
            z-index: 10;
            opacity: 0;
            transition: all 300ms ease;
            @include susy-breakpoint($s-320, $c8) {
                width: 2rem;
                height: 2rem;
                transform: translate3d(-200%,-50%,0);
            }
            @include susy-breakpoint($s-768, $c8) {
                width: 2.4rem;
                height: 2.4rem;
                transform: translate3d(-192%,-50%,0);
            }
            @include susy-breakpoint($s-960, $c12) {
                width: 2.4em;
                height: 2.4rem;
                transform: translate3d(-192%,-50%,0);
            }
            @include susy-breakpoint($s-1201, $c18) {
                width: 2.4rem;
                height: 2.4rem;
                transform: translate3d(-225%,-50%,0);
            }
        }
        &:hover {
            background-color: #fff;
            span {
                color: #20c405;
            }
            img {
                opacity: 0;
            }
            &::before {
                opacity: 1;
            }
        }
    }
    &__shoe {
        position: absolute;
        left: 50%;
        transform: translate3d(-50%,0,0);
        bottom: 16%;
        @include susy-breakpoint($s-320, $c8) {
            bottom: 13%;
        }
        @include susy-breakpoint($s-960, $c12) {
            bottom: 14%;
        }
        img {
            width: 36rem;
            display: block;
            margin: 0;
            @include susy-breakpoint($s-320, $c8) {
                width: 30rem;
            }
        }
    }
}