.section-app {
  width: 100%;
  background: #000 url('../images/app-bg.jpg') repeat-x center top;
  background-size: auto 100%;
  height: 124.4rem;
  box-sizing: border-box;
  padding-top: 11rem;
  @include susy-breakpoint($s-320, $c8) {
    background-image: url('../images/app-bg-m.jpg');
    background-size: 41.4rem auto;
    height: 94rem;
    padding-top: 6.4rem;
  }
  @include susy-breakpoint($s-960, $c12) {
    background-size: 145rem auto;
    padding-top: 5.6rem;
    height: 90rem;
  }
  @include susy-breakpoint($s-1201, $c18) {
    background-size: 145rem auto;
    padding-top: 5.6rem;
    height: 90rem;
  }
  &__wrap {
    width: 98rem;
    margin: 0 auto;
    @include susy-breakpoint($s-320, $c8) {
      width: 100%;
      box-sizing: border-box;
      padding: 0 2rem;
    }
    @include susy-breakpoint($s-768, $c8) {
      width: 70rem;
    }
  }
  &__title {
    font-size: 3.6rem;
    text-align: center;
    color: #fff;
    margin-bottom: 6.4rem;
    @include susy-breakpoint($s-320, $c8) {
      font-size: 2rem;
      margin-bottom: 3.2rem;
    }
    @include susy-breakpoint($s-960, $c12) {
      margin-bottom: 3.2rem;
    }
    @include susy-breakpoint($s-1201, $c18) {
      margin-bottom: 3.2rem;
    }
  }
  &__feature {
    margin-bottom: 6.4rem;
    @include susy-breakpoint($s-320, $c8) {
      margin-bottom: 3.2rem;
    }
    @include susy-breakpoint($s-960, $c12) {
      margin-bottom: 3.2rem;
    }
    @include susy-breakpoint($s-1201, $c18) {
      margin-bottom: 3.2rem;
    }
    p {
      display: flex;
      justify-content: center;
      align-items: center;
      &:not(:last-child) {
        margin-bottom: 3.2rem;
        @include susy-breakpoint($s-320, $c8) {
          margin-bottom: 1.6rem;
        }
        @include susy-breakpoint($s-960, $c12) {
          margin-bottom: 1.6rem;
        }
      }
    }
    span {
      font-size: 3.6rem;
      color: #fff;
      strong {
        font-weight: normal;
        color: #e4dfa3;
      }
      @include susy-breakpoint($s-320, $c8) {
        font-size: 2rem;
      }
      @include susy-breakpoint($s-960, $c12) {
        font-size: 2.4rem;
      }
      @include susy-breakpoint($s-1201, $c18) {
        font-size: 2.4rem;
      }
    }
    img {
      width: 4rem;
      @include susy-breakpoint($s-320, $c8) {
        width: 2.4rem;
      }
      @include susy-breakpoint($s-960, $c12) {
        width: 3.2rem;
      }
      @include susy-breakpoint($s-1201, $c18) {
        width: 3.2rem;
      }
    }
  }
  &__pd {
    margin-bottom: 4.8rem;
    @include susy-breakpoint($s-960, $c12) {
      width: 56rem;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 2.4rem;
    }
    @include susy-breakpoint($s-1201, $c18) {
      width: 56rem;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 2.4rem;
    }
    img {
      max-width: 100%;
      display: block;
    }
  }
  &__app {
    box-sizing: border-box;
    padding: 2rem;
    width: 44rem;
    border: solid 0.1rem #fff;
    border-radius: 1.6rem;
    margin: 0 auto 4.8rem;
    @include susy-breakpoint($s-320, $c8) {
      width: 100%;
      margin-bottom: 2.4rem;
    }
    h3 {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-bottom: 2.4rem;
      > img {
        width: 2.4rem;
      }
      > span {
        font-size: 1.8rem;
        color: #fff;
      }
    }
  }
  &__app-wrap {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    @include susy-breakpoint($s-320, $c8) {
      gap: 1.6rem;
    }
    a {
      width: 100%;
      display: block;
      transition: all 300ms ease;
      &:hover {
        transform: translate3d(0,-5%,0);
      }
      img {
        max-width: 100%;
        display: block;
        margin: 0;
      }
    }
  }
  &__btn-home {
    box-sizing: border-box;
    padding: 2rem;
    width: 44rem;
    border: solid 0.1rem #fff;
    border-radius: 1.6rem;
    display: flex;
    position: relative;
    margin: 0 auto;
    justify-content: center;
    align-items: center;
    gap: 0.8rem;
    > img {
      width: 2.4rem;
    }
    > span {
      font-size: 1.8rem;
      color: #fff;
    }
    @include susy-breakpoint($s-320, $c8) {
      width: 100%;
      > img {
        width: 2rem;
      }
      > span {
        font-size: 1.6rem;
        color: #fff;
      }
    }
  }
}