.section-wish {
    $self: &;
    background: #4d26b3 url('../images/wish-bg.jpg') repeat-x center top;
    background-size: cover;
    padding: 12rem 0 0;
    position: relative;
    box-sizing: border-box;
    height: 108rem;
    @include susy-breakpoint($s-320, $c8) {
        background-size: auto 100%;
        padding: 8rem 0 0;
        height: 74rem;
    }
    @include susy-breakpoint($s-768, $c8) {
        height: 80rem;
    }
    @include susy-breakpoint($s-960, $c12) {
        height: 80rem;
    }
    @include susy-breakpoint($s-1201, $c18) {
        height: 80rem;
    }
    @include susy-breakpoint($s-1601, $c18) {
        height: 108rem;
    }
    &__title {
        font-size: 5.6rem;
        text-align: center;
        color: #fff;
        line-height: 1.6;
        position: relative;
        z-index: 250;
        padding-left: 2rem;
        padding-right: 2rem;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 1.8rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            font-size: 3.2rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            font-size: 3.2rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            font-size: 3.2rem;
        }
    }
    &__shoe-slide-wrap {
        position: relative;
        z-index: 0;
    }
    .wishShoeSwiper {
        height: 72rem;
        @include susy-breakpoint($s-960, $c12) {
            height: 64rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            height: 64rem;
        }
        .swiper-slide {
            cursor: pointer;
            box-sizing: border-box;
            position: relative;
            padding: 12rem 0 0;
            pointer-events: none;
            @include susy-breakpoint($s-320, $c8) {
                padding-top: 6.4rem;
            }
        }
        .swiper-slide-active {
            pointer-events: auto;
            #{$self}__pick {
                opacity: 1;
            }
            #{$self}__shoe {
                transform: scale(1);
            }
            #{$self}__intro {
                opacity: 1;
            }
        }
        .swiper-button-prev {
            width: 6.4rem;
            height: 6.4rem;
            background: transparent url('../images/btn-prev@3x.png') no-repeat 0 0;
            background-size: cover;
            left: 50%;
            top: 50%;
            transform: translate3d(-550%,-200%,0);
            &:after {
                display: none;
            }
            @include susy-breakpoint($s-320, $c8) {
                display: none;
            }
            @include susy-breakpoint($s-768, $c8) {
                display: none;
            }
            @include susy-breakpoint($s-960, $c12) {
                width: 4rem;
                height: 4rem;
                transform: translate3d(-520%,-280%,0);
            }
            @include susy-breakpoint($s-1201, $c18) {
                width: 4rem;
                height: 4rem;
                transform: translate3d(-580%,-280%,0);
            }
        }
        .swiper-button-next {
            width: 6.4rem;
            height: 6.4rem;
            background: transparent url('../images/btn-next@3x.png') no-repeat 0 0;
            background-size: cover;
            right: inherit;
            left: 50%;
            top: 50%;
            transform: translate3d(450%,-200%,0);
            &:after {
                display: none;
            }
            @include susy-breakpoint($s-320, $c8) {
                display: none;
            }
            @include susy-breakpoint($s-768, $c8) {
                display: none;
            }
            @include susy-breakpoint($s-960, $c12) {
                width: 4rem;
                height: 4rem;
                transform: translate3d(420%,-280%,0);
            }
            @include susy-breakpoint($s-1201, $c18) {
                width: 4rem;
                height: 4rem;
                transform: translate3d(480%,-280%,0);
            }
        }
        .swiper-pagination {
            z-index: 300;
            bottom: inherit;
            top: 53%;
            transform: translate3d(0,0,0);
            @include susy-breakpoint($s-320, $c8) {
                top: 31%;
            }
            @include susy-breakpoint($s-768, $c8) {
                top: 42%;
            }
            @include susy-breakpoint($s-960, $c12) {
                top: 42%;
            }
            @include susy-breakpoint($s-1201, $c18) {
                top: 44%;
            }
        }
        .swiper-pagination-bullet {
            width: 1.6rem;
            height: 1.6rem;
            background-color: #fff;
            opacity: 0.6;
            margin-left: 0.8rem !important;
            margin-right: 0.8rem !important;
            @include susy-breakpoint($s-320, $c8) {
                width: 0.8rem;
                height: 0.8rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                width: 0.8rem;
                height: 0.8rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                width: 0.8rem;
                height: 0.8rem;
            }
            @include susy-breakpoint($s-1201, $c18) {
                width: 0.8rem;
                height: 0.8rem;
            }
        }
        .swiper-pagination-bullet-active {
            opacity: 1;
        }
    }
    &__pick {
        width: 26rem;
        height: 17rem;
        background: transparent url('../images/btn-pick@2x.png') no-repeat 0 0;
        background-size: cover;
        position: absolute;
        left: 50%;
        top: -2rem;
        transform: translate3d(-50%,20%,0);
        opacity: 0;
        transition: all 1000ms ease;
        animation-name: PickMoveUpDown;
        animation-duration: 1000ms;
        animation-iteration-count: infinite;
        animation-timing-function: ease-in-out;
        animation-direction: alternate;
        @include susy-breakpoint($s-320, $c8) {
            width: 26rem * 0.5;
            height: 17rem * 0.5;
        }
        @include susy-breakpoint($s-768, $c8) {
            width: 26rem * 0.6;
            height: 17rem * 0.6;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 26rem * 0.6;
            height: 17rem * 0.6;
        }
        @include susy-breakpoint($s-1201, $c18) {
            width: 26rem * 0.7;
            height: 17rem * 0.7;
        }
    }
    &__shoe {
        width: 48rem;
        transform-origin: 50% 90%;
        transform: scale(0.7);
        margin: 0 auto 5.6rem;
        transition: all 500ms ease;
        pointer-events: none;
        position: relative;
        z-index: 0;
        img {
            max-width: 100%;
            display: block;
            margin: 0;
            pointer-events: none;
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 30rem;
            margin-bottom: 3.2rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            width: 32rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 29rem;
            margin-bottom: 4rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            width: 29rem;
            margin-bottom: 4rem;
        }
    }
    &__intro {
        position: relative;
        z-index: 0;
        opacity: 0.5;
        transition: all 300ms ease;
        padding: 0 4rem;
        h3 {
            font-size: 3.6rem;
            line-height: 1.6;
            color: #fff;
            margin-bottom: 1.6rem;
            text-align: center;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 2rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                font-size: 2rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 2.4rem;
            }
            @include susy-breakpoint($s-1201, $c18) {
                font-size: 2.4rem;
            }
            @include susy-breakpoint($s-1601, $c18) {
                font-size: 2.8rem;
            }
        }
        p {
            font-size: 2.4rem;
            line-height: 1.6;
            color: #fff;
            text-align: center;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.4rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                font-size: 1.6rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 1.6rem;
            }
            @include susy-breakpoint($s-1201, $c18) {
                font-size: 1.6rem;
            }
            @include susy-breakpoint($s-1601, $c18) {
                font-size: 2rem;
            }
        }
    }
    &__btn-choose {
        top: 0;
        left: 0;
        position: absolute;
        width: 100%;
        height: 100%;
        padding: 0;
        background-color: transparent;
        border: 0;
        z-index: 100;
        background-color: rgba(40,244,120,0.5);
        cursor: pointer;
    }
    &__chosen {
        box-sizing: border-box;
        padding-top: 34rem;
        background: #4c24af url('../images/wish-chosen-bg.jpg') repeat-x center center;
        background-size: cover;
        height: 100%;
        width: 100%;
        position: absolute;
        z-index: 240;
        left: 0;
        top: 0;
        pointer-events: none;
        opacity: 0;
        transition: all 800ms ease;
        &.active {
            opacity: 1;
            pointer-events: auto;
        }
        @include susy-breakpoint($s-320, $c8) {
            background-image: url('../images/wish-chosen-bg-m.jpg');
            background-size: 41.4rem auto;
            background-position: center top;
            padding-top: 9.6rem;
            z-index: 260;
        }
        @include susy-breakpoint($s-768, $c8) {
            padding-top: 26rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            padding-top: 24rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            padding-top: 24rem;
        }
        @include susy-breakpoint($s-1601, $c18) {
            padding-top: 34rem;
        }
    }
    &__chosen-inner {
        width: 120rem;
        margin: 0 auto;
        box-sizing: border-box;
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
            padding-left: 2rem;
            padding-right: 2rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            width: 100%;
            padding-left: 2rem;
            padding-right: 2rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 88rem;
            padding-left: 2rem;
            padding-right: 2rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            width: 96rem;
            padding-left: 2rem;
            padding-right: 2rem;
        }
    }
    &__chosen-shoe {
        width: 64rem;
        margin: 0 auto 2.4rem;
        transition: all 500ms ease;
        img {
            max-width: 100%;
            display: block;
            margin: 0;
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 30rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            width: 40rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 44rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            width: 50rem;
        }
    }
    &__chosen-wrap {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 2.4rem;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 2.4rem;
        width: 56rem;
        @include susy-breakpoint($s-320, $c8) {
            margin-bottom: 1.6rem;
            gap: 1.6rem;
            width: 28rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            gap: 2.4rem;
            width: 40rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            gap: 2.4rem;
            width: 40rem;
            margin-bottom: 2.4rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            gap: 2.4rem;
            width: 48rem;
            margin-bottom: 2.4rem;
        }
    }
    &__btn-reselect,
    &__btn-select {
        border-radius: 8rem;
        background-color: #fff;
        font-size: 3.2rem;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #4600a7;
        font-weight: 900;
        transition: all 300ms ease;
        padding: 2rem 0;
        &:hover {
            transform: translate3d(0,-5%,0);
        }
        @include susy-breakpoint($s-320, $c8) {
            font-size: 1.6rem;
            padding-top: 1.2rem;
            padding-bottom: 1.2rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            font-size: 2rem;
            padding-top: 1.6rem;
            padding-bottom: 1.6rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            font-size: 2.4rem;
            padding-top: 1.6rem;
            padding-bottom: 1.6rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            font-size: 2.4rem;
        }
    }
    &__chosen-intro {
        h3 {
            font-size: 3.2rem;
            line-height: 1.6;
            color: #fff;
            margin-bottom: 0.8rem;
            text-align: center;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 2rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                font-size: 2rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 2.4rem;
            }
            @include susy-breakpoint($s-1201, $c18) {
                font-size: 2.4rem;
            }
            @include susy-breakpoint($s-1601, $c18) {
                font-size: 2.8rem;
            }
        }
        p {
            font-size: 2rem;
            line-height: 1.6;
            color: #fff;
            text-align: center;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.4rem;
                word-wrap: break-word;
                text-align: justify;
                text-justify: inter-ideograph;
            }
            @include susy-breakpoint($s-768, $c8) {
                font-size: 1.6rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 1.6rem;
            }
            @include susy-breakpoint($s-1201, $c18) {
                font-size: 1.6rem;
            }
            @include susy-breakpoint($s-1601, $c18) {
                font-size: 1.8rem;
            }
        }
    }
}

@keyframes PickMoveUpDown {
    0%,
    100% {
        transform: translate3d(-50%,20%,0);
    }
    50% {
        transform: translate3d(-50%,15%,0);
    }
}