.section-rule {
    padding: 12rem 0 70rem;
    background: #110026 url('../images/rule-bg.jpg') repeat center top;
    background-size: 192rem auto;
    position: relative;
    @include susy-breakpoint($s-320, $c8) {
        background-image: url('../images/rule-bg-m.jpg');
        background-size: 41.4rem auto;
        padding-top: 5.6rem;
        padding-bottom: 30rem;
        .container {
            padding-left: 2rem;
            padding-right: 2rem;
        }
    }
    @include susy-breakpoint($s-960, $c12) {
        background-size: 145rem auto;
        padding-bottom: 56rem;
    }
    @include susy-breakpoint($s-1201, $c18) {
        background-size: 145rem auto;
        padding-bottom: 56rem;
    }
    &::after {
        width: 100%;
        content: "";
        left: 0;
        bottom: 0;
        position: absolute;
        height: 70rem;
        background: #110026 url('../images/rule-bottom-bg.jpg') repeat center top;
        background-size: 192rem auto;
        z-index: 1;
        @include susy-breakpoint($s-320, $c8) {
            background-image: url('../images/rule-bottom-bg-m.jpg');
            background-size: 41.4rem auto;
            height: 30rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            background-size: 145rem auto;
            height: 56rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            background-size: 145rem auto;
            height: 56rem;
        }
    }
    &__title {
        @extend %section-title;
        margin-bottom: 10rem;
        @include susy-breakpoint($s-320, $c8) {
            margin-bottom: 4.8rem;
        }
    }
    &__item {
        &:not(:last-child) {
            margin-bottom: 5.6rem;
            @include susy-breakpoint($s-320, $c8) {
                margin-bottom: 3.2rem;
            }
        }
        h4 {
            font-size: 1.8rem;
            text-align: center;
            color: #fff;
            margin-bottom: 4rem;
            font-weight: 900;
            @include susy-breakpoint($s-320, $c8) {
                margin-bottom: 2.4rem;
            }
        }
        p {
            text-align: center;
            color: #fff;
            line-height: 1.6;
            font-weight: 400;
            font-size: 1.8rem;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.4rem;
            }
        }
    }
    &__step-wrap {
        border: solid 0.1rem #fff;
        &:not(:last-child) {
            margin-bottom: 3.2rem;
            @include susy-breakpoint($s-320, $c8) {
                margin-bottom: 2.4rem;
            }
        }
    }
    &__step {
        display: flex;
        &:not(:last-child) {
            border-bottom: solid 0.1rem #fff;
        }
        @include susy-breakpoint($s-320, $c8) {
            flex-wrap: wrap;
        }
        h5 {
            flex-basis: 10rem;
            padding: 1.6rem 0;
            text-align: center;
            color: #fff;
            line-height: 1.6;
            font-weight: 400;
            font-size: 1.8rem;
            border-right: solid 0.1rem #fff;
            align-self: stretch;
            display: flex;
            align-items: center;
            justify-content: center;
            @include susy-breakpoint($s-320, $c8) {
                flex-basis: 100%;
                font-size: 1.6rem;
                border-right: 0;
                border-bottom: solid 0.1rem #fff;
            }
        }
        p {
            flex: 1 0 0;
            padding: 1.6rem;
            text-align: left;
            @include susy-breakpoint($s-320, $c8) {
                flex-basis: 100%;
                font-size: 1.4rem;
                box-sizing: border-box;
            }
            a {
                color: #00f000;
                transition: color 300ms ease;
                &:hover {
                    color: #fff;
                }
            }
        }
    }
    &__ol-list {
        counter-reset: ol-list;
        li {
            counter-increment: ol-list;
            color: #fff;
            line-height: 1.6;
            font-weight: 400;
            font-size: 1.8rem;
            position: relative;
            padding-left: 2.2rem;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.4rem;
                padding-left: 1.6rem;
            }
            &::before {
                content: counter(ol-list)".";
                line-height: 1.6;
                font-weight: 400;
                font-size: 1.8rem;
                position: absolute;
                left: 0;
                top: 0;
                @include susy-breakpoint($s-320, $c8) {
                    font-size: 1.4rem;
                }
            }
            &:not(:last-child) {
                margin-bottom: 1.6rem;
            }
        }
        &.rule {
            li {
                font-size: 1.4rem;
                &:not(:last-child) {
                    margin-bottom: 1rem;
                }
                &::before {
                    font-size: 1.4rem;
                }
            }
        }
    }
}