.preloader {
    width: 100%;
    height: 100%;
    position: fixed;
    background-color: #000;
    z-index: 500;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    pointer-events: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 1000ms ease;
    &.loaded {
        opacity: 0;
        pointer-events: none;
    }
    &__content {
        width: 20rem;
    }
    &__logo {
        background: #000 url('../images/preloader-logo.jpg') no-repeat 0 0;
        background-size: cover;
        width: 20rem;
        height: 2.8rem;
        margin-bottom: 1.6rem;
    }
    &__range {
        width: 100%;
        height: 0.4rem;
        background-color: #222;
        border-radius: 1rem;
        margin-bottom: 1.6rem;
        position: relative;
        box-sizing: border-box;
    }
    &__range-inner {
        position: absolute;
        width: 0;
        height: 0.4rem;
        background-color: #fff;
        border-radius: 0.2rem;
        left: 0;
        top: 0;
    }
    &__text {
        font-size: 1.4rem;
        color: #fff;
        text-align: center;
    }
}