.winner-modal {
    position: fixed;
    width: 100%;
    height: 100vh;
    left: 0;
    top: 0;
    z-index: 500;
    opacity: 0;
    transition: all 300ms ease;
    pointer-events: none;
    will-change: opacity;
    &.active {
        pointer-events: auto;
        opacity: 1;
    }
    &__overlay {
        width: 100%;
        height: 100%;
        background-color: rgba(0,0,0,0.9);
        overflow-y: auto;
    }
    &__content {
        box-sizing: border-box;
        position: relative;
        max-width: 56rem;
        margin: 0 auto;
        padding-top: 5.6rem;
        padding-bottom: 5.6rem;
        @include susy-breakpoint($s-320, $c8) {
            max-width: inherit;
            width: 100%;
            padding-left: 2rem;
            padding-right: 2rem;
        }
    }
    &__btn-close {
        width: 64px;
        height: 64px;
        -webkit-appearance: none;
        cursor: pointer;
        text-decoration: none;
        text-align: center;
        padding: 0;
        color: #fff;
        border: 0;
        background: none;
        -webkit-box-shadow: none;
        box-shadow: none;
        border-radius: 50%;
        border: solid 1px #fff;
        position: relative;
        margin: 0 auto;
        display: block;
        @include susy-breakpoint($s-320, $c8) {
            width: 48px;
            height: 48px;
        }
        &::before,
        &::after {
            width: 32px;
            height: 1px;
            background-color: #fff;
            position: absolute;
            left: 50%;
            top: 50%;
            content: "";
            @include susy-breakpoint($s-320, $c8) {
                width: 24px;
            }
        }
        &::before {
            transform: translate3d(-50%,-50%,0) rotate(45deg);
        }
        &::after {
            transform: translate3d(-50%,-50%,0) rotate(-45deg);
        }
    }
    &__block {
        padding-top: 5.6rem;
        h3 {
            text-align: center;
            font-size: 3.2rem;
            color: #fff;
            margin-bottom: 3.6rem;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 2.4rem;
                margin-bottom: 2.4rem;
            }
        }
    }
    &__item {
        border-radius: 2.4rem;
        border: solid 0.1rem #8c8c8c;
        padding: 3.2rem 2rem;
        &:not(:last-child) {
            margin-bottom: 2.4rem;
        }
    }
    &__row {
        display: flex;
        gap: 2rem;
        margin-bottom: 2rem;
        justify-content: center;
        @include susy-breakpoint($s-320, $c8) {
            flex-wrap: wrap;
            gap: 0.8rem;
        }
    }
    &__email,
    &__mobile {
        font-size: 2rem;
        color: #fff;
        line-height: 1.6;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 1.8rem;
        }
    }
    &__email {
        text-align: right;
    }
    &__shoe {
        width: 28rem;
        margin: 0 auto;
        img {
            max-width: 100%;
            display: block;
            margin: 0;
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
        }
    }
    &__winner {
        h4 {
            text-align: center;
            font-size: 2.4rem;
            color: #fff;
            margin-bottom: 1.6rem;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 2rem;
                margin-bottom: 0.8rem;
            }
        }
        &:nth-child(2) {
            position: relative;
            padding-top: 4.8rem;
            &::before {
                content: "";
                left: 50%;
                top: 0;
                transform: translate3d(-50%,0,0);
                position: absolute;
                width: 0.6rem;
                height: 0.6rem;
                border-top: solid 0.1rem #fff;
                border-right: solid 0.1rem #fff;
                transform: translate3d(-50%,1.6rem,0) rotate(-45deg);
            }
            &::after {
                content: "";
                position: absolute;
                left: 50%;
                top: 0;
                transform: translate3d(-50%,0,0);
                width: 0.1rem;
                height: 1.6rem;
                background-color: #fff;
                transform: translate3d(-50%,1.6rem,0);
            }
        }
    }
}