.section-login {
    background: #4c05be url('../images/chance-bg.jpg') repeat-x center bottom;
    background-size: 192rem auto;
    width: 100%;
    height: 120rem;
    position: relative;
    z-index: 300;
    box-sizing: border-box;
    transition: all 500ms ease;
    @include susy-breakpoint($s-320, $c8) {
        background-image: url('../images/chance-bg-m.jpg');
        background-size: 41.4rem auto;
        height: 78rem;
    }
    @include susy-breakpoint($s-768, $c8) {
        background-size: 192rem auto;
        padding-bottom: 20rem;
        height: 104rem;
    }
    @include susy-breakpoint($s-960, $c12) {
        background-size: 145rem auto;
        height: 84rem;
    }
    @include susy-breakpoint($s-1201, $c18) {
        background-size: 145rem auto;
        height: 86rem;
    }
    @include susy-breakpoint($s-1601, $c18) {
        height: 108rem;
    }
    &__inner {
        padding-top: 24rem;
        width: 64rem;
        margin: 0 auto;
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
            box-sizing: border-box;
            padding-left: 8vw;
            padding-right: 8vw;
            padding-top: 8rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            padding-top: 12rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            background-size: 145rem auto;
            padding-top: 12rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            background-size: 145rem auto;
            padding-top: 12rem;
        }
        @include susy-breakpoint($s-1601, $c18) {
            padding-top: 14rem;
        }
    }
    &__sub-title {
        @extend %site-sub-title;
    }
    &__description {
        @extend %site-description;
    }
    &__text {
        @extend %site-text;
    }
    &__btn-wrap {
        @extend %site-btn-wrap;
    }
    &__item {
        flex: 1;
        > p {
            @extend %site-text;
            margin-bottom: 0;
        }
    }
    &__btn {
        @extend %site-btn-w;
    }
    &__shoe {
        position: absolute;
        left: 50%;
        transform: translate3d(-50%,0,0);
        bottom: 18%;
        @include susy-breakpoint($s-320, $c8) {
            bottom: 20%;
        }
        @include susy-breakpoint($s-768, $c8) {
            bottom: 20%;
        }
        @include susy-breakpoint($s-960, $c12) {
            bottom: 18%;
        }
        @include susy-breakpoint($s-1201, $c18) {
            bottom: 18%;
        }
        @include susy-breakpoint($s-1601, $c18) {
            bottom: 20%;
        }
        img {
            transform: scale(0);
            width: 36rem;
            display: block;
            margin: 0;
            @include susy-breakpoint($s-320, $c8) {
                width: 28rem;
            }
        }
    }
}