.modal {
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.9);
    pointer-events: none;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    z-index: 400;
    transition: all 500ms ease;
    &.active {
        opacity: 1;
        pointer-events: auto;
    }
    &__content {
        position: relative;
        width: 80rem;
        @include susy-breakpoint($s-320, $c8) {
            width: calc(100% - 4rem);
        }
        @include susy-breakpoint($s-768, $c8) {
            width: 70rem;
        }
    }
    &__video-frame {
        width: 100%;
        padding-bottom: 56.25%;
        height: 0;
        position: relative;
        iframe,
        video {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }
    }
    &__btn-close {
        width: 64px;
        height: 64px;
        position: absolute;
        top: 0;
        left: 50%;
        transform: translate3d(-50%,-100%,0);
        -webkit-appearance: none;
        cursor: pointer;
        text-decoration: none;
        text-align: center;
        padding: 0;
        color: #fff;
        border: 0;
        background: none;
        -webkit-box-shadow: none;
        box-shadow: none;
        border-radius: 50%;
        border: solid 1px #fff;
        transform: translate3d(-50%,-140%,0);
        @include susy-breakpoint($s-320, $c8) {
            width: 48px;
            height: 48px;
        }
        &::before,
        &::after {
            width: 32px;
            height: 1px;
            background-color: #fff;
            position: absolute;
            left: 50%;
            top: 50%;
            content: "";
            @include susy-breakpoint($s-320, $c8) {
                width: 24px;
            }
        }
        &::before {
            transform: translate3d(-50%,-50%,0) rotate(45deg);
        }
        &::after {
            transform: translate3d(-50%,-50%,0) rotate(-45deg);
        }
    }
}