.section-video {
    background: #000 url('../images/video-bg.jpg') repeat-x center top;
    background-size: 192rem auto;
    height: 94.8rem;
    display: flex;
    justify-content: center;
    position: relative;
    @include susy-breakpoint($s-320, $c8) {
        height: 38rem;
        background-image: url('../images/video-bg-m.jpg');
        background-size: 41.4rem auto;
        background-position: center bottom;
    }
    @include susy-breakpoint($s-768, $c8) {
        height: 64rem;
        background-position: center bottom;
    }
    @include susy-breakpoint($s-960, $c12) {
        background-size: 145rem auto;
        height: 70rem;
    }
    @include susy-breakpoint($s-1201, $c18) {
        background-size: 145rem auto;
        height: 70rem;
    }
    &__wrap {
        position: absolute;
        top: 10%;
        left: 50%;
        transform: translate3d(-50%,0,0);
        width: 117.8rem;
        z-index: 100;
        .swiper {
            padding-bottom: 3.2rem;
            @include susy-breakpoint($s-768, $c8) {
                padding-bottom: 4.8rem;
            }
        }
        @include susy-breakpoint($s-320, $c8) {
            width: calc(100% - 4rem);
            top: 7rem;
            transform: translate3d(-50%,0,0);
        }
        @include susy-breakpoint($s-768, $c8) {
            width: 70rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 90rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            width: 90rem;
        }
    }
    &__video-wrap {
        h2 {
            @extend %section-title;
            br {
                display: none;
                @include susy-breakpoint($s-320, $c8) {
                    display: block;
                }
            }
        }
        h3 {
            @extend %section-sub-title;
            &.tp {
                opacity: 0;
            }
        }
    }
    &__btn-play {
        width: 98rem;
        display: block;
        margin: 0 auto;
        position: relative;
        overflow: hidden;
        &::after {
            content: "";
            width: 8.6rem;
            height: 8.6rem;
            background: transparent url('../images/btn-play@3x.png') no-repeat 0 0;
            background-size: cover;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate3d(-50%,-50%,0);
            z-index: 20;
        }
        &::before {
            content: "";
            width: 100%;
            height: 100%;
            background-color: rgba(0,0,0,0.15);
            position: absolute;
            left: 0;
            top: 0;
            z-index: 10;
            transition: all 1000ms ease;
        }
        img {
            @extend .rsp-img;
            transition: all 1000ms ease;
        }
        &:hover {
            &::before {
                opacity: 0;
            }
            img {
                transform: scale(1.05);
            }
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
            &::before {
                content: "";
                width: 100%;
                height: 100%;
                background-color: rgba(0,0,0,0.3);
                position: absolute;
                left: 0;
                top: 0;
                z-index: 10;
                transition: all 1000ms ease;
                opacity: 0;
            }
            &::after {
                width: 3.2rem;
                height: 3.2rem;
            }
            &:hover {
                &::before {
                    opacity: 0;
                }
                img {
                    transform: scale(1);
                }
            }
        }
        @include susy-breakpoint($s-768, $c8) {
            width: 100%;
            &::after {
                width: 5.6rem;
                height: 5.6rem;
            }
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 70rem;
            &::after {
                width: 5.6rem;
                height: 5.6rem;
            }
        }
        @include susy-breakpoint($s-1201, $c18) {
            width: 70rem;
            &::after {
                width: 5.6rem;
                height: 5.6rem;
            }
        }
    }
    &__video-slide-wrap {
        .swiper-button-prev {
            width: 6.4rem;
            height: 6.4rem;
            background: transparent url('../images/btn-prev@3x.png') no-repeat 0 0;
            background-size: cover;
            left: 2rem;
            margin-top: 2rem;
            &:after {
                display: none;
            }
            @include susy-breakpoint($s-320, $c8) {
                display: none;
            }
            @include susy-breakpoint($s-768, $c8) {
                display: none;
            }
            @include susy-breakpoint($s-1201, $c18) {
                left: 0;
                &::after {
                    width: 5.6rem;
                    height: 5.6rem;
                }
            }
        }
        .swiper-button-next {
            width: 6.4rem;
            height: 6.4rem;
            background: transparent url('../images/btn-next@3x.png') no-repeat 0 0;
            background-size: cover;
            right: 2rem;
            margin-top: 2rem;
            &:after {
                display: none;
            }
            @include susy-breakpoint($s-320, $c8) {
                display: none;
            }
            @include susy-breakpoint($s-768, $c8) {
                display: none;
            }
            @include susy-breakpoint($s-1201, $c18) {
                right: 0;
                &::after {
                    width: 5.6rem;
                    height: 5.6rem;
                }
            }
        }
        .swiper-pagination {
            bottom: 0;
            @include susy-breakpoint($s-768, $c8) {
                bottom: 1rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                bottom: 1rem;
            }
        }
        .swiper-pagination-bullet {
            width: 1.6rem;
            height: 1.6rem;
            background-color: #fff;
            opacity: 0.6;
            margin-left: 0.8rem !important;
            margin-right: 0.8rem !important;
            @include susy-breakpoint($s-320, $c8) {
                width: 0.8rem;
                height: 0.8rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                width: 0.8rem;
                height: 0.8rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                width: 0.8rem;
                height: 0.8rem;
            }
        }
        .swiper-pagination-bullet-active {
            opacity: 1;
        }
    }
}